.header {
    column-count: 4;
    column-gap: 30px;
    
}

.header__item {
display: flex;

}   
.header__link:hover {
    cursor: pointer;
    background-color: bisque;
    text-decoration: underline;
}

.header__link{
    display: flex;
        align-items: center;
        justify-content: center;
        font-size: 20px;
        font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
        text-align: center;
        margin-bottom: 30px;
        width: 200px;
            height: 200px;
            background-color: antiquewhite;
        text-decoration: none;
            list-style: none;
            text-decoration: none;
            color: black;
}